import React from 'react';
import PropTypes from 'prop-types';

import Lines from 'components/EditorComponents/Lines/Lines.js';
import Message from 'components/EditorComponents/Message/Message.js';
import Signal from 'components/EditorComponents/Signal/Signal.js';
import Box from 'components/EditorComponents/Box/Box.js';
import Speaker from 'components/EditorComponents/Speaker/Speaker.js';
import Meta17820 from 'components/EditorComponents/Meta17820/Meta17820.js';
import Meta18580 from 'components/EditorComponents/Meta18580/Meta18580.js';

import classes from './Editor.module.scss';

class Editor extends React.Component {
  getSchemeItem = (item, index) => {
    const { scheme } = this.props;

    switch (item.type) {
      case 'lines': {
        return (
          <Lines
            key={`${item.id}-${index}`}
            settings={scheme.settings}
            lines={item.lines}
            onChange={this.props.onEditorChange}
          />
        );
      }
      case 'message': {
        return <Message key={`${item.id}-${index}`} item={item} />;
      }
      case 'signal': {
        return (
          <Signal
            key={`${item.id}-${index}`}
            item={item}
            onChange={this.props.onEditorChange}
          />
        );
      }
      case 'box': {
        return <Box key={`${item.id}-${index}`} item={item} />;
      }
      case 'speaker': {
        return <Speaker key={`${item.id}-${index}`} item={item} />;
      }
      case 'meta17820': {
        return (
          <Meta17820
            key={`${item.id}-${index}`}
            item={item}
            schemeID={scheme.id}
            LCDprops={this.props.scheme.meta17820}
            onChange={this.props.onEditorChange}
          />
        );
      }
      case 'meta18580': {
        return (
          <Meta18580
            key={item.id}
            item={item}
            onChange={this.props.onEditorChange}
          />
        );
      }
      default:
        return null;
    }
  };
  render() {
    const { scheme } = this.props;

    // если схемы нет ничего не рисуем
    if (!scheme) {
      return null;
    }
    const lineStyle = {
      width: scheme.settings.width,
      height: scheme.settings.height
    };

    const message = [];
    // eslint-disable-next-line
    for (let key in scheme.message) {
      message.push(scheme.message[key]);
    }

    return (
      <div className={classes.main} style={lineStyle}>
        {/* TODO fix {...it} */}
        {scheme &&
          scheme.elements.map((it, index) =>
            this.getSchemeItem({ ...it }, index)
          )}
        {message.map((it, index) => this.getSchemeItem(it, index))}
      </div>
    );
  }
}

Editor.propTypes = {
  scheme: PropTypes.object,
  onEditorChange: PropTypes.func
};

export default Editor;
