import React from 'react';
import { connect } from 'react-redux';
import { setPopup } from 'store/actions/popup';
import LayoutPopup from 'layout/LayoutPopup/LayoutPopup';
import PopupSetSound from './PopupSetSound/PopupSetSound';

const getPopup = (popupName, onClose) => {
  switch (popupName) {
    case 'setSound':
      return <PopupSetSound onClose={onClose} />;
    default:
      return null;
  }
};

class Popup extends React.PureComponent {
  onClose = () => {
    this.props.setPopup('');
  };
  render() {
    const { popup } = this.props;

    if (!popup) {
      return null;
    }

    return (
      <LayoutPopup id="Popup" onClose={this.onClose}>
        {getPopup(popup, this.onClose)}
      </LayoutPopup>
    );
  }
}

const mapStateToProps = state => ({
  popup: state.popup.popup
});

const mapDispatchToProps = {
  setPopup
};

export default connect(mapStateToProps, mapDispatchToProps)(Popup);
