import {
  SCHEME_SET_SETTING,
  SCHEME_SET_META18580,
  SCHEME_SET_FIRE_SIGNALS,
  SCHEME_SET_GOI4S_SIGNALS,
  SCHEME_SET_SCHEMES
} from '../actionTypes';
import { meta18580 } from 'utils/func/getScheme/meta17820.js';

const INIT_SETTINGS = { algoritm: '1' };

const initialState = {
  fireSignals: [],
  goI4SSignals: false,
  globalSettings: INIT_SETTINGS,
  meta18580: meta18580.get(),
  isSchemeDefault: null, // for render new scheme
  mainProps: {}, // for render new scheme
  schemes: [] // for render new scheme
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SCHEME_SET_SETTING:
      return {
        ...state,
        globalSettings: action.payload
      };
    case SCHEME_SET_META18580:
      return {
        ...state,
        meta18580: action.payload
      };
    case SCHEME_SET_FIRE_SIGNALS:
      return {
        ...state,
        fireSignals: action.payload
      };
    case SCHEME_SET_GOI4S_SIGNALS:
      return {
        ...state,
        goI4SSignals: action.payload
      };
    case SCHEME_SET_SCHEMES:
      return {
        ...state,
        isSchemeDefault: action.payload.isSchemeDefault,
        mainProps: action.payload.mainProps,
        schemes: action.payload.schemes
      };
    default:
      return state;
  }
};
