import React from 'react';
import PropTypes from 'prop-types';

import Btn1LED from 'components/EditorComponents/Btn1LED/Btn1LED.js';
import LED from 'components/EditorComponents/LED/LED.js';

import classes from './PanelControls.module.scss';

const widths = {
  8: '150px',
  16: '130px',
  24: '120px',
  32: '120px'
};

class PanelControls extends React.Component {
  onMicControlsClick = (id, value) => {
    const micControls = this.props.micControls;
    micControls[id] = value;

    this.props.onChange(`micControls`, micControls);
  };
  onControlsClick = (id, value) => {
    const controls = this.props.controls;
    controls[id] = value;

    this.props.onChange(`controls`, controls);
  };
  render() {
    const { lineNumber, controls = [] } = this.props;
    const lineStyle = {
      width: widths[lineNumber] || widths[8]
    };
    return (
      <div className={classes.main} style={lineStyle}>
        <div className={classes.micControls}>
          <Btn1LED
            item={this.props.micControls}
            onChange={this.onMicControlsClick}
          />
        </div>

        <div className={classes.controls}>
          {this.props.LEDs.map((it, i) => (
            <div key={i} className={classes.controls_itemLEDs}>
              <LED {...it} />
            </div>
          ))}
          {controls.map((it, i) => (
            <div key={i} className={classes.controls_item}>
              <Btn1LED item={it} onChange={this.onControlsClick} />
            </div>
          ))}
        </div>
      </div>
    );
  }
}

PanelControls.propTypes = {
  micControls: PropTypes.object.isRequired,
  LEDs: PropTypes.array.isRequired,
  controls: PropTypes.array.isRequired,
  onChange: PropTypes.func.isRequired
};

export default PanelControls;
