import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import {
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  Slider,
  Paper
} from '@material-ui/core';

import { setVolume } from 'store/actions/sound.js';
import { setSettings } from 'store/actions/scheme.js';

import classes from './PanelSettingsMeta17820.module.scss';

class PanelSettings extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      volumeValue: this.props.volumeValue
    };
  }

  onAlgoritmClick = (event, newAlgoritm) => {
    this.onSettingsChange(`algoritm`, newAlgoritm);
  };

  onIncreaseDisplayChange = (event, volumeValue) => {
    this.onSettingsChange(`increaseDisplay`, volumeValue);
  };

  onSettingsChange = (field, value) => {
    this.props.setSettings({ ...this.props.globalSettings, [field]: value });
  };

  onVolumeValueChange = (event, volumeValue) => {
    this.setState(() => ({ volumeValue }));
    this.props.setVolume(volumeValue);
  };

  onVolumeValueSet = (event, volumeValue) => {
    this.props.setVolume(volumeValue);
  };

  render() {
    const { globalSettings } = this.props;

    return (
      <div className={classes.main}>
        <Paper className={classes.PaperBlock}>
          <FormControl component="fieldset">
            <FormLabel component="legend" className={classes.formLabel}>
              Выбор алгоритма работы
            </FormLabel>
            <RadioGroup
              aria-label="position"
              name="position"
              value={globalSettings.algoritm || '1'}
              onChange={this.onAlgoritmClick}
              row
            >
              <FormControlLabel
                value="1"
                control={<Radio color="primary" />}
                label="1"
                labelPlacement="top"
              />
              <FormControlLabel
                value="2"
                control={<Radio color="primary" />}
                label="2"
                labelPlacement="top"
              />
              <FormControlLabel
                value="3"
                control={<Radio color="primary" />}
                label="3"
                labelPlacement="top"
              />
            </RadioGroup>
          </FormControl>
        </Paper>

        <Paper className={classes.PaperBlock}>
          <FormControl component="fieldset">
            <FormLabel component="legend" className={classes.formLabel}>
              Управление звуком
            </FormLabel>
            <div className={classes.sliderContainer}>
              <Slider
                value={this.state.volumeValue}
                aria-labelledby="label"
                onChange={this.onVolumeValueChange}
                onDragEnd={this.onVolumeValueSet}
              />
            </div>
          </FormControl>
        </Paper>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  volumeValue: state.sound.volumeValue,
  globalSettings: state.scheme.globalSettings
});

const mapDispatchToProps = {
  setVolume,
  setSettings
};

PanelSettings.propTypes = {
  settings: PropTypes.object,
  volumeValue: PropTypes.number,
  setSettings: PropTypes.func,
  setVolume: PropTypes.func
};

export default connect(mapStateToProps, mapDispatchToProps)(PanelSettings);
