import React from 'react';
import { connect } from 'react-redux';
import LayoutPage from 'layout/LayoutPage/LayoutPage.js';
import { Typography } from '@material-ui/core';
import { setMainBlock } from 'store/actions/configurator.js';
import ActivePaperBtn from 'components/UI/ActivePaperBtn/ActivePaperBtn';
import PanelMeta17820 from './PanelMeta17820/PanelMeta17820';
import PanelButtons from './PanelButtons/PanelButtons';
import PanelShema from './PanelShema/PanelShema';
import PanelSpecifications from './PanelSpecifications/PanelSpecifications';
import classes from './PageConfigurator.module.scss';

import meta17821Img from 'assets/img/components/17821_m.png';

class PageConfigurator extends React.PureComponent {
  constructor(props) {
    super(props);

    const blocks = [
      {
        title: `МЕТА 17820`,
        img: meta17821Img,
        imgTitle: `МЕТА 17820/17821`,
        imgAlt: `МЕТА 17820/17821`
      },
      {
        title: `МЕТА 17821`,
        img: meta17821Img,
        imgTitle: `МЕТА 17820/17821`,
        imgAlt: `МЕТА 17820/17821`
      }
    ];

    this.state = {
      blocks
    };
  }

  componentDidMount() {
    // this.props.setMainBlock(''); // TODO del //
  }

  onActiveBlockChange = activeBlock => {
    this.props.setMainBlock(activeBlock);
  };

  render() {
    const { blocks } = this.state;
    const { configurator = {} } = this.props;
    const showMeta17820 =
      configurator.mainBlock === 'МЕТА 17820' ||
      configurator.mainBlock === 'МЕТА 17821';

    return (
      <LayoutPage>
        <div className={classes.main}>
          <Typography variant="h1" gutterBottom>
            Конфигуратор
          </Typography>
          <p className={classes.blockTitle}>Выберите блок</p>

          <div className={classes.blockStep}>
            {blocks.map(it => (
              <div key={it.title} className={classes.blockStepItem}>
                <ActivePaperBtn
                  isActive={configurator.mainBlock === it.title}
                  {...it}
                  width="150px"
                  onChange={this.onActiveBlockChange}
                />
              </div>
            ))}
          </div>

          {showMeta17820 && <PanelMeta17820 />}

          {configurator.configured && <PanelButtons />}

          {configurator.showType === 'shema' && <PanelShema />}

          {configurator.showType === 'specifications' && (
            <PanelSpecifications />
          )}
        </div>
      </LayoutPage>
    );
  }
}

const mapStateToProps = state => ({
  configurator: state.configurator
});

const mapDispatchToProps = {
  setMainBlock
};

export default connect(mapStateToProps, mapDispatchToProps)(PageConfigurator);
