import React from 'react';
import { useDispatch } from 'react-redux';
import { ButtonBase } from '@material-ui/core';
import { setShowType } from 'store/actions/configurator.js';
import classes from './PanelButtons.module.scss';

const PanelButtons = () => {
  const dispatch = useDispatch();

  return (
    <div className={classes.panelBtn}>
      <ButtonBase
        focusRipple
        className={classes.setSirensBtn}
        onClick={() => dispatch(setShowType('shema'))}
      >
        <p>ПОКАЗАТЬ СХЕМУ</p>
      </ButtonBase>

      <ButtonBase
        focusRipple
        className={classes.setSirensBtn}
        onClick={() => dispatch(setShowType('specifications'))}
      >
        <p>ПОКАЗАТЬ СПЕЦИФИКАЦИЮ</p>
      </ButtonBase>
    </div>
  );
};

export default PanelButtons;
