import { TIME_SET } from '../actionTypes';

let lastTimerID;

export const setTime = payload => {
  return {
    type: TIME_SET,
    payload
  };
};

// const getLineCB = time =>
//   `ЦБ  ${time.h}:${time.m}:${time.s} ${time.d}.${time.month}.${time.y}         `;

const getTimeStr = time =>
  `${time.h}:${time.m}:${time.s} ${time.d}.${time.month}.${time.y}`;

const getLineCB = timeStr => `ЦБ  ${timeStr}         `;
const getLineBR = timeStr => `БР  ${timeStr}         `;

export const initTime = () => dispatch => {
  const now = new Date();
  const time = {
    h: now.getHours(),
    m: now.getMinutes(),
    s: now.getSeconds(),
    d: now.getDate(),
    month: now.getMonth() + 1,
    y: now.getFullYear() % 100
  };

  if (time.s < 10) time.s = `0${time.s}`;
  if (time.h < 10) time.h = `0${time.h}`;
  if (time.m < 10) time.m = `0${time.m}`;
  if (time.d < 10) time.d = `0${time.d}`;
  if (time.month < 10) time.month = `0${time.month}`;

  const timeStr = getTimeStr(time);

  dispatch(
    setTime({
      time,
      timeToTextLine: getLineCB(timeStr),
      timeToTextLineBR: getLineBR(timeStr)
    })
  );

  lastTimerID = setTimeout(() => {
    dispatch(calcTime());
  }, 1000);
};

const calcTime = () => (dispatch, getState) => {
  const { time = {} } = getState().time;
  const now = new Date();

  let s = now.getSeconds();
  if (s < 10) s = `0${s}`;

  if (time.s !== s) {
    time.s = s;

    if (s === `00`) {
      time.h = now.getHours();
      time.m = now.getMinutes();
      if (time.h < 10) time.h = `0${time.h}`;
      if (time.m < 10) time.m = `0${time.m}`;

      if (time.m === `00`) {
        time.d = now.getDate();
        time.month = now.getMonth() + 1;
        time.y = now.getFullYear() % 100;

        if (time.d < 10) time.d = `0${time.d}`;
        if (time.month < 10) time.month = `0${time.month}`;
      }
    }

    const timeStr = getTimeStr(time);

    dispatch(
      setTime({
        time,
        timeToTextLine: getLineCB(timeStr),
        timeToTextLineBR: getLineBR(timeStr)
      })
    );
  }

  lastTimerID = setTimeout(() => {
    clearTimeout(lastTimerID);
    dispatch(calcTime());
  }, 100);
};
