import React from 'react';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';

import SpeakerIcon from 'assets/img/components/speaker.svg';

import classes from './Speaker.module.scss';

/**
{
    type: `speaker`,
    top: 10,
    left: 870,
    soundActive: false, // распространение звука
    text: `Зона 1`,
},
 */
class Speaker extends React.Component {
  render() {
    const { item } = this.props;

    const lineStyle = {
      top: item.top || ``,
      left: item.left || ``
    };

    const clsText = item.textAlign === `left` ? classes.textLeft : classes.text; // default right

    return (
      <div className={classes.main} style={lineStyle}>
        <img
          className={classes.img}
          src={SpeakerIcon}
          title={item.imgTitle || `Speaker`}
          alt={item.imgAlt || `Speaker`}
        />
        <Typography variant="body1" className={clsText}>
          {item.text}
        </Typography>

        {item.soundActive && <div className={classes.soundActive}></div>}
      </div>
    );
  }
}

Speaker.propTypes = {
  item: PropTypes.object
};

export default Speaker;
