import { setTrack } from 'store/actions/sound.js';
import getScheme, {
  getDefultMainPropsMeta17820
} from 'utils/func/getScheme/meta17820.js';
import {
  getNewMainProps,
  getCheckScheme,
  getMeta18580
} from 'utils/func/getScheme/meta17820Change.js';
import {
  SCHEME_SET_SETTING,
  SCHEME_SET_META18580,
  SCHEME_SET_FIRE_SIGNALS,
  SCHEME_SET_GOI4S_SIGNALS,
  SCHEME_SET_SCHEMES
} from '../actionTypes';
let lastTimerID = null;

export const setSettings = settings => ({
  type: SCHEME_SET_SETTING,
  payload: settings
});

export const setMeta18580 = meta18580 => ({
  type: SCHEME_SET_META18580,
  payload: meta18580
});

export const setFireSignals = signals => ({
  type: SCHEME_SET_FIRE_SIGNALS,
  payload: signals
});

export const setGo4ISSignals = signal => ({
  type: SCHEME_SET_GOI4S_SIGNALS,
  payload: signal
});

export const setSchemes = ({ mainProps, schemes, isSchemeDefault }) => ({
  type: SCHEME_SET_SCHEMES,
  payload: { mainProps, schemes, isSchemeDefault }
});

const soundTurnOff = () => (dispatch, getState) => {
  const { soundOn } = getState().sound;

  // sound turn off
  if (soundOn) {
    dispatch(setTrack(``, true));
  }
};

const setMeta18580FromScheme = (schemes = [{}]) => dispatch => {
  // update meta18580
  const scheme = schemes[0];
  const { elements } = scheme;

  const meta18580 = elements[23];

  dispatch(setMeta18580(meta18580));
};

export const setDefaultScheme = () => (dispatch, getState) => {
  const lineNumber = 8;
  const mainProps = getDefultMainPropsMeta17820(lineNumber);
  const schemes = [
    getScheme({
      index: 0,
      activeNumberPult: 1,
      deletePult: false,
      lineToNextBlock: false,
      lineNumber
    })
  ];

  dispatch(setMeta18580FromScheme(schemes));

  const isSchemeDefault = true;

  dispatch(soundTurnOff());
  dispatch(setSchemes({ mainProps, schemes, isSchemeDefault }));
};

export const setNewScheme = () => (dispatch, getState) => {
  const { configurator = {} } = getState();
  const {
    config: { extraBlocks = [], activeNumberPult, lineNumber }
  } = configurator;

  const numberBlock = extraBlocks.length + 1;
  const schemes = [];

  for (let i = 0; i < numberBlock; i++) {
    schemes.push(
      getScheme({
        index: i,
        activeNumberPult: i === 0 && activeNumberPult,
        deletePult: i !== 0,
        lineToNextBlock: i < numberBlock - 1,
        lineNumber
      })
    );
  }

  dispatch(setMeta18580FromScheme(schemes));

  const mainProps = getDefultMainPropsMeta17820(lineNumber);
  const isSchemeDefault = false;

  dispatch(soundTurnOff());
  dispatch(setSchemes({ mainProps, schemes, isSchemeDefault }));
};

const setSounds2ForFire = () => (dispatch, getState) => {
  const { schemes, mainProps, isSchemeDefault } = getState().scheme;
  const newMainProps = { ...mainProps };
  newMainProps.setSounds2ForFire = true;

  dispatch(
    setSchemes({
      mainProps: newMainProps,
      schemes,
      isSchemeDefault
    })
  );

  dispatch(schemeChange());
};

const setNewSound = newMainProps => (dispatch, getState) => {
  // установка нового звука, если звук совпадает со старым то продолжит играть старый трек
  if (newMainProps.activeZone.includes(true)) {
    dispatch(setTrack(newMainProps.track, newMainProps.loopNewSound));
  } else {
    const { soundOn, fileData } = getState().sound;
    if (soundOn && fileData) {
      dispatch(setTrack(`fileData`, true));
    } else {
      // off sound
      dispatch(setTrack(``, false));
    }
  }
};

// обработка изменения из схемы
export const schemeChange = (schemeID = 0, signalID, value) => (
  dispatch,
  getState
) => {
  const {
    schemes,
    mainProps,
    meta18580,
    globalSettings,
    isSchemeDefault
  } = getState().scheme;

  // новые глобальные параметры всей схемы - что нажато в каком режиме отображать
  const newMainProps = getNewMainProps({
    mainProps,
    meta18580,
    globalSettings,
    schemeID,
    signalID,
    value
  });

  // update meta18580
  const newMeta18580 = getMeta18580({ mainProps: newMainProps, meta18580 });
  dispatch(setMeta18580(newMeta18580));

  // установка нового звука, если звук совпадает со старым то продолжит играть старый трек
  dispatch(setNewSound(newMainProps));

  if (newMainProps.startTimerToSet2Fire) {
    // через 30 сек запуск лругого сообщения
    clearTimeout(lastTimerID); // сначала удалить старый таймаут
    lastTimerID = setTimeout(() => {
      dispatch(setSounds2ForFire());
    }, 10000); // TODO изменить на значение 30 сек
  }

  const newSchemes = schemes.map((it, i) => {
    const { scheme } = getCheckScheme({
      schemeOld: it,
      meta18580,
      schemeIndex: i,
      mainProps: newMainProps,
      globalSettings,
      signal: {
        schemeID,
        signalID,
        value
      }
    });

    return scheme;
  });

  dispatch(
    setSchemes({
      mainProps: newMainProps,
      schemes: newSchemes,
      isSchemeDefault
    })
  );
};

