import React from 'react';
import { connect } from 'react-redux';
import { setSirene } from 'store/actions/sirene';
import sirensList from 'utils/const/list/sirensList';
import { ButtonBase } from '@material-ui/core';
import Select from 'components/UI/Select/Select';
import classes from './SelectSiren.module.scss';

const getList = (arr, field) => {
  if (field === 'type') {
    return [...new Set(arr.map(it => it[field]))].sort(); // sort ABC
  }
  return [...new Set(arr.map(it => it[field]))].sort(
    (a, b) => Number(a) - Number(b)
  );
};

const getTypeSirens = (state, field) => {
  if (!state) {
    return [];
  }
  let newArr = [...sirensList];

  if (state.type && field !== 'type') {
    newArr = newArr.filter(it => it.type === state.type);
  }
  if (state.inputPower && field !== 'inputPower') {
    newArr = newArr.filter(it => it.inputPower === state.inputPower);
  }
  if (state.sensitivity && field !== 'sensitivity') {
    newArr = newArr.filter(it => it.sensitivity === state.sensitivity);
  }

  return getList(newArr, field);
};

const initState = {
  type: ``,
  typeList: getList(sirensList, 'type'),
  forExampleShowTypeList: [],
  inputPower: ``,
  inputPowerList: getList(sirensList, 'inputPower'),
  sensitivity: ``,
  sensitivityList: getList(sirensList, 'sensitivity')
};

class SelectSiren extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = initState;
  }

  handleSetSireneClick = state => {
    if (!(state && state.type && state.inputPower && state.sensitivity)) {
      return;
    }
    this.props.setSirene({
      type: state.type,
      sensitivity: state.sensitivity,
      inputPower: state.inputPower
    });
  };

  handleResetClick = () => this.setState(() => initState);

  handleSelectChange = (e, fieldName) => {
    this.setState(prevState => {
      const newState = {
        ...prevState,
        [fieldName]: e.target.value
      };

      if (fieldName === 'type') {
        // reset field after change type
        newState.sensitivity = '';
        newState.inputPower = '';
      }

      newState.sensitivityList = getTypeSirens(newState, 'sensitivity');

      newState.sensitivity =
        newState.sensitivityList.length === 1
          ? newState.sensitivityList[0]
          : newState.sensitivity;

      newState.inputPowerList = getTypeSirens(newState, 'inputPower');
      newState.inputPower =
        newState.inputPowerList.length === 1
          ? newState.inputPowerList[0]
          : newState.inputPower;

      if (newState.sensitivity !== '' && newState.inputPower !== '') {
        newState.forExampleShowTypeList = getTypeSirens(newState, 'type');
        newState.type = newState.forExampleShowTypeList[0];
        newState.sensitivityList = [newState.sensitivity];
        newState.inputPowerList = [newState.inputPower];
      } else {
        newState.forExampleShowTypeList = [];
      }

      this.handleSetSireneClick(newState);

      return newState;
    });
  };

  render() {
    const { handleCancelBtnClick } = this.props;
    const forExampleShowTypeList = this.state.forExampleShowTypeList.join(', ');

    return (
      <div className={classes.main}>
        <p className={classes.title}>Выбирете параметры оповещателя</p>

        <div className={classes.panelSelect}>
          <div className={classes.selectItem}>
            <Select
              title="Тип"
              value={this.state.type}
              valueList={this.state.typeList}
              fieldName="type"
              onChange={this.handleSelectChange}
            />
          </div>

          <div className={classes.selectItem}>
            <Select
              title="Чувствительность"
              value={this.state.sensitivity}
              valueList={this.state.sensitivityList}
              fieldName="sensitivity"
              onChange={this.handleSelectChange}
            />
          </div>

          <div className={classes.selectItem}>
            <Select
              title="Электрическая мощность"
              value={this.state.inputPower}
              valueList={this.state.inputPowerList}
              fieldName="inputPower"
              onChange={this.handleSelectChange}
            />
          </div>

          <ButtonBase
            focusRipple
            className={classes.resetBtn}
            onClick={this.handleResetClick}
          >
            <p>Сброс</p>
          </ButtonBase>
        </div>

        {forExampleShowTypeList && (
          <div className={classes.panelBtn}>
            <span className={classes.spanText}>Подходят оповещатели:</span>
            <span className={classes.spanText}>{forExampleShowTypeList}</span>
          </div>
        )}

        {handleCancelBtnClick && (
          <div className={classes.panelBtn}>
            <ButtonBase
              focusRipple
              className={classes.cancelBtn}
              onClick={this.handleCancelBtnClick}
            >
              <p>Закрыть панель выбора оповещателя</p>
            </ButtonBase>
          </div>
        )}

        {/* old show table
      <div className={classes.rowHeader}>
        <p className={classes.cell1}>Тип оповещателя</p>
        <p className={classes.cell2}>N0</p>
        <p className={classes.cell3}>Pэ</p>
      </div>

      {sirensList.map(it => (
        <div
          key={`${it.type}_${it.inputPower}`}
          className={classes.row}
          onClick={() => dispatch(setSirene(it))}
        >
          <p className={classes.cell1}>{it.type}</p>
          <p className={classes.cell2}>{it.sensitivity}</p>
          <p className={classes.cell3}>{it.inputPower}</p>
        </div>
      ))} */}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  sirene: state.sirene.sirene
});

const mapDispatchToProps = {
  setSirene
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SelectSiren);
