import { TIME_SET } from '../actionTypes';

const initialState = {
  time: {},
  timeToTextLine: '',
  timeToTextLineBR: ''
};

export default (state = initialState, action) => {
  switch (action.type) {
    case TIME_SET: {
      const { time = {}, timeToTextLine, timeToTextLineBR } = action.payload;
      return {
        ...state,
        time,
        timeToTextLine,
        timeToTextLineBR
      };
    }

    default:
      return state;
  }
};
