import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { schemeChange } from 'store/actions/scheme';
import Editor from 'components/Editor/Editor.js';

class SchemeMeta17820 extends React.PureComponent {
  // обработка изменения из схемы
  onEditorChange = (id, value) => {
    this.props.schemeChange(this.props.schemeID, id, value);
  };

  render() {
    const { schemes, schemeID } = this.props;

    if (!schemes || typeof schemeID !== 'number' || !Array.isArray(schemes)) {
      return null;
    }

    const scheme = schemes[schemeID];

    return scheme ? (
      <Editor scheme={scheme} onEditorChange={this.onEditorChange} />
    ) : null;
  }
}

const mapStateToProps = state => ({
  schemes: state.scheme.schemes
});

const mapDispatchToProps = {
  schemeChange
};

SchemeMeta17820.propTypes = {
  // props
  schemeID: PropTypes.number,
  // store
  schemes: PropTypes.array,
  // actions
  schemeChange: PropTypes.func
};

export default connect(mapStateToProps, mapDispatchToProps)(SchemeMeta17820);
