import React from 'react';
import { connect } from 'react-redux';
import { TextField } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import configurator from 'mockData/configurator.js'; // TODO del  закомментировать
import { ButtonBase } from '@material-ui/core';
import { setNewConfig } from 'store/actions/configurator.js';
import ActivePaperBtn from 'components/UI/ActivePaperBtn/ActivePaperBtn';
import Select from 'components/UI/Select/Select';
import sirensList from 'utils/const/list/sirensList';
import PanelMeta17820ExtraBlock from './PanelMeta17820ExtraBlock/PanelMeta17820ExtraBlock';
import classes from './PanelMeta17820.module.scss';

const numberPults = [
  {
    title: `1`
  },
  {
    title: `2`
  }
];

const CssTextField = withStyles({
  root: {
    '& fieldset': {
      borderColor: '#2196f3',
      borderWidth: 1
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#2196f3',
        borderWidth: 2
      }
    }
  }
})(TextField);

const SIRENS_TYPE_LIST = [...new Set(sirensList.map(it => it.type))].sort();

// TODO change init_Config in actions store
const INIT_CONFIG = {
  activePowerBlock: `200`,
  extraBlocks: [],
  activeNumberBlock: 1,
  lineNumber: 8,
  activeNumberPult: ``,
  sirensType: '',
  activeNumberSirene: '',
  showStepSetNumberBlocks: false,
  showStepSetNumberPults: false,
  ...configurator.config // TODO del  закомментировать
};

class PanelMeta17820 extends React.PureComponent {
  componentDidMount() {
    this.props.setNewConfig({
      ...INIT_CONFIG,
      // mainBlock: this.props.mainBlock // TODO del  раскомментировать
      mainBlock: configurator.mainBlock // TODO del  закомментировать
    });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.mainBlock !== this.props.mainBlock) {
      // TODO del  раскомментировать
      // this.props.setNewConfig({
      //   ...INIT_CONFIG,
      //   mainBlock: this.props.mainBlock
      // });
    }
  }

  onExtraBlocksAdd = () => {
    const extraBlock = [
      {
        active: true,
        title: `МЕТА 17820`,
        imgTitle: `МЕТА 17820/17821`,
        imgAlt: `МЕТА 17820/17821`
      },
      {
        active: false,
        title: `МЕТА 17821`,
        imgTitle: `МЕТА 17820/17821`,
        imgAlt: `МЕТА 17820/17821`
      }
    ];

    const {
      config: { extraBlocks = [] }
    } = this.props;

    const newExtraBlocks = [...extraBlocks, extraBlock];
    const activeNumberBlock = newExtraBlocks.length + 1;

    this.props.setNewConfig({
      ...this.props.config,
      extraBlocks: newExtraBlocks,
      activeNumberBlock,
      lineNumber: activeNumberBlock * 8
    });
  };

  onExtraBlocksDelete = index => {
    const {
      config: { extraBlocks }
    } = this.props;
    const newExtraBlocks = [...extraBlocks];
    newExtraBlocks.splice(index, 1);

    const activeNumberBlock = newExtraBlocks.length + 1;

    this.props.setNewConfig({
      ...this.props.config,
      extraBlocks: newExtraBlocks,
      activeNumberBlock,
      lineNumber: activeNumberBlock * 8
    });
  };

  onActiveNumberPultChange = activeNumberPult => {
    this.props.setNewConfig({
      ...this.props.config,
      activeNumberPult
    });
  };

  onActiveBlockChange = (index, title) => {
    const {
      config: { extraBlocks }
    } = this.props;
    const newExtraBlocks = [...extraBlocks];
    newExtraBlocks[index] = newExtraBlocks[index].map(it => ({
      ...it,
      active: it.title === title
    }));

    this.props.setNewConfig({
      ...this.props.config,
      extraBlocks: newExtraBlocks
    });
  };

  onActiveNumberSireneChange = e => {
    const value = +e.target.value;
    if (value < 0) return;

    this.props.setNewConfig({
      ...this.props.config,
      activeNumberSirene: String(value)
    });
  };

  handleSelectChange = e => {
    const { activeNumberSirene: activeNumberSireneStore } = this.props.config;
    const activeNumberSirene = activeNumberSireneStore
      ? activeNumberSireneStore
      : 1;

    this.props.setNewConfig({
      ...this.props.config,
      sirensType: e.target.value,
      activeNumberSirene
    });
  };

  handleDeleteSirense = e => {
    this.props.setNewConfig({
      ...this.props.config,
      sirensType: '',
      activeNumberSirene: ''
    });
  };

  render() {
    const { mainBlock, config = {} } = this.props;
    const {
      extraBlocks = [],
      activeNumberBlock,
      lineNumber,
      activeNumberPult,
      activeNumberSirene,
      sirensType
    } = config;

    const P = mainBlock === 'МЕТА 17820' ? '200' : '500';
    return (
      <div className={classes.main}>
        <p className={classes.blockHelpTitle}>
          У блока
          <span className={classes.blockHelpTitleBold}>{mainBlock}</span>
          номинальная выходная мощность
          <span className={classes.blockHelpTitleBold}>{P}</span>Вт
        </p>

        <div className={classes.block}>
          <p className={classes.blockHelpTitle}>
            Можно выбрать до 4 блоков расширения
          </p>

          <div className={classes.extraBlocks}>
            {extraBlocks.map((it, i) => (
              <PanelMeta17820ExtraBlock
                key={`extraBlock_${i}`}
                blocks={it}
                index={i}
                onActiveBlockChange={this.onActiveBlockChange}
                onExtraBlocksDelete={this.onExtraBlocksDelete}
              />
            ))}
          </div>

          {activeNumberBlock < 5 && (
            <div className={classes.blockStep}>
              <ActivePaperBtn
                isActive
                title="+"
                width="60px"
                onChange={this.onExtraBlocksAdd}
              />
            </div>
          )}

          <p className={classes.blockHelpTitle}>
            1 блок дает 8 линий речевого оповещения
          </p>

          <p className={classes.blockHelpTitle}>
            Количетсво блоков в системе
            <span className={classes.blockHelpTitleBold}>
              {activeNumberBlock}
            </span>
            шт.
          </p>

          <p className={classes.blockHelpTitle}>
            Количетсво линий речевого оповещения
            <span className={classes.blockHelpTitleBold}>{lineNumber}</span>
            шт.
          </p>
        </div>

        <div className={classes.block}>
          <p className={classes.blockTitle}>Выберите количество пультов</p>
          <div className={classes.blockStep}>
            {numberPults.map(it => (
              <div key={it.title} className={classes.blockStepItem}>
                <ActivePaperBtn
                  isActive={activeNumberPult === it.title}
                  {...it}
                  width="70px"
                  onChange={this.onActiveNumberPultChange}
                />
              </div>
            ))}
          </div>
        </div>

        {activeNumberPult && (
          <div className={classes.block}>
            <p className={classes.blockTitle}>
              Выберите тип оповещателя и количество оповещателей
            </p>
            <div className={classes.blockSirens}>
              <div className={classes.blockSirensItem}>
                <Select
                  title="Тип"
                  value={sirensType}
                  valueList={SIRENS_TYPE_LIST}
                  fieldName="type"
                  onChange={this.handleSelectChange}
                />
              </div>

              <CssTextField
                style={{ width: 120, marginLeft: 8, marginRight: 8 }}
                label="Кол-во"
                value={activeNumberSirene}
                type="number"
                variant="outlined"
                onChange={this.onActiveNumberSireneChange}
              />

              {activeNumberSirene !== '' && (
                <ButtonBase
                  focusRipple
                  className={classes.resetBtn}
                  onClick={this.handleDeleteSirense}
                >
                  <p>Сброс</p>
                </ButtonBase>
              )}
            </div>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  mainBlock: state.configurator.mainBlock,
  config: state.configurator.config
});

const mapDispatchToProps = {
  setNewConfig
};

export default connect(mapStateToProps, mapDispatchToProps)(PanelMeta17820);
