import React from 'react';
import PropTypes from 'prop-types';
import classes from './LayoutPopup.module.scss';

class LayoutPopup extends React.PureComponent {
  componentDidMount() {
    document.addEventListener('keydown', this.onKeyPress);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.onKeyPress);
  }

  onKeyPress = e => {
    if (e.key === 'Escape') {
      this.onClosePopup();
    }
  };

  onClosePopup = () => {
    const { onClose } = this.props;

    if (onClose) {
      onClose();
    }
  };

  onClick = e => {
    const { id } = this.props;

    if (e.target.id === id) {
      this.onClosePopup();
    }
  };

  render() {
    const { id, children } = this.props;

    return (
      <div className={classes.main}>
        <div className={classes.layout} id={id} onClick={this.onClick}>
          {children}
        </div>
      </div>
    );
  }
}

LayoutPopup.propTypes = {
  topAlign: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node
  ])
};

export default LayoutPopup;
