export const getFillArr = (length, fillvalue = false) => {
  const result = [];
  result.length = length;
  result.fill(fillvalue);
  return result;
};

// 1) vvv props for change scheme vvv
export const getDefultMainPropsMeta17820 = (lineNumber = 8) => {
  const arrFalse = getFillArr(lineNumber, false);

  return {
    lineNumber,

    activeZone: arrFalse, // работающие динамики и плюсики на экране
    fireZone: arrFalse, // зоны в которых пожар
    activePultZone: arrFalse, // зоны выбранные на пульте

    // обрыв линии
    lineToSpeakerErr: arrFalse, // к динамику
    lineToFireErr: arrFalse, // к пожару
    lineToGoI4SErr: false, // к ГО и ЧС
    connectToPult: true, // связь с пультом
    connectToBoxAKB: true, // связь с боксом АКБ
    connectToPower: true, // связь с 220В

    pultSignal: false, // сигнал c пульта META18580 // отображение строки "ПУЛЬТ" на LCD
    blocking: false, // блокирвка на пульте META18580
    fire: false, // пожар
    goI4S: false, // ГО и ЧС
    problem: false, // неисправность
    power: false, // питание
    offZummer: false, // откл зумерра
    offVolume: false, // откл звука
    signal: false, // сигнал

    algoritm: 1, //
    setSounds2ForFire: false, //
    signalStartSystem: false,
    message: {
      id: `${Math.random()}`,
      soundZones: {
        type: `message`,
        top: 500,
        left: 870,
        text: ``,
        width: 150
      }
    }
  };
};
