// pult
export const meta18580 = {
  getZoneControls(lineNumber = 8) {
    let result = [];
    result.length = lineNumber;

    return result.fill({}).map((it, i) => ({
      id: i,
      LEDColor: `green`,
      text: `${i + 1}`,
      on: false
    }));
  },

  getLeft(lineNumber = 8) {
    switch (lineNumber) {
      case 8:
        return 50;
      case 16:
        return 0;
      case 24:
        return -60;
      case 32:
        return -120;
      default:
        return 50;
    }
  },

  get(lineNumber, nextPult) {
    return {
      // 23
      type: `meta18580`,
      top: nextPult ? 850 : 580,
      left: this.getLeft(lineNumber),
      textName1: `Пульт микрофонный`,
      textName2: `МЕТА 18580`,
      keyOn: false,
      lineNumber: lineNumber || 8,
      LEDs: [
        {
          text: `НЕИСПР`,
          LEDColor: `red`,
          on: false
        },
        {
          text: `СВЯЗЬ`,
          LEDColor: `green`,
          on: true
        }
      ],
      micControls: [
        // микрофон
        {
          id: 0,
          btnColor: `red`,
          LEDColor: `green`,
          text: `МИКР`,
          on: false
        }
      ],
      controls: [
        {
          id: 0,
          btnColor: `red`,
          LEDColor: `green`,
          text: `СИРЕНА`,
          on: false
        },
        {
          id: 1,
          LEDColor: `green`,
          text: `ОБЩ`,
          on: false
        }
      ],
      zoneControls: this.getZoneControls(lineNumber || 8)
    };
  }
};

