export const getNewMainProps = ({
  mainProps,
  meta18580: meta18580Arg,
  globalSettings,
  schemeID,
  signalID,
  value
}) => {
  const meta18580 = value && value.type === `meta18580` ? value : meta18580Arg;
  const newMainProps = { ...mainProps };

  // пришли новые настроки схемы от родителя
  if (globalSettings) {
    newMainProps.algoritm = globalSettings.algoritm;
  }

  // reset activeZone
  newMainProps.activeZone = getFillArr(newMainProps.lineNumber, false);

  if (typeof schemeID !== 'undefined' && typeof value !== 'undefined') {
    switch (value.type) {
      case `lines`: // check lines breakPoint
        value.lines.forEach(item => {
          if (item.breakPoint) {
            switch (item.name) {
              case `toLineFire`: {
                newMainProps.lineToFireErr = [...newMainProps.lineToFireErr];
                newMainProps.lineToFireErr[item.fireID] =
                  item.breakPoint.active;
                break;
              }
              case `toSpeaker`: {
                newMainProps.lineToSpeakerErr = [
                  ...newMainProps.lineToSpeakerErr
                ];
                newMainProps.lineToSpeakerErr[item.speakerID] =
                  item.breakPoint.active;
                break;
              }
              case `toGoI4S`: {
                newMainProps.lineToGoI4SErr = item.breakPoint.active;
                break;
              }
              case `toMeta18580`: {
                newMainProps.connectToPult = !item.breakPoint.active;
                break;
              }
              case `toMeta17901`: {
                newMainProps.connectToBoxAKB = !item.breakPoint.active;
                break;
              }
              case `toPowerLine`: {
                newMainProps.connectToPower = !item.breakPoint.active;
                break;
              }
              default:
                break;
            }
          }
        });
        break;
      case `signal`: // checkFire
        if (value) {
          switch (value.text[0]) {
            case `П`: {
              // сигнал пожара
              newMainProps.fireZone = [...newMainProps.fireZone];
              newMainProps.fireZone[value.zoneID] = value.active;
              break;
            }
            case `Г`: {
              // сигнал ГО и ЧС
              newMainProps.goI4S = value.active;
              break;
            }
            default:
              break;
          }
        }

        break;
      case `meta18580`:
        // вкл блокировка на пульте
        if (value.keyOn) {
          newMainProps.blocking = true;
        }
        break;
      default:
        break;
    }
  }

  // check fire signal
  // not signal fire in this line
  newMainProps.fire = newMainProps.fireZone
    .map((it, i) => !newMainProps.lineToFireErr[i] && it)
    .includes(true);

  // check problem in scheme
  newMainProps.problem =
    newMainProps.lineToSpeakerErr.includes(true) ||
    newMainProps.lineToFireErr.includes(true) ||
    newMainProps.lineToGoI4SErr ||
    !newMainProps.connectToPult ||
    !newMainProps.connectToBoxAKB ||
    !newMainProps.connectToPower;

  // check power system = connect to power
  newMainProps.power =
    newMainProps.connectToBoxAKB || newMainProps.connectToPower;

  // check pult button
  // зона активна если активна кнопка зоны или общая кнопка
  newMainProps.activePultZone = meta18580.zoneControls.map(
    it => it.on || meta18580.controls[1].on
  );

  newMainProps.track = ``; // нет звука,
  newMainProps.loopNewSound = true; // нет звука,

  // если есть питание, соеденение с блоком,
  // если нажата кнопка микр или сирена
  // если на пульте выбранна зона
  const isActivePult = () =>
    newMainProps.connectToPult &&
    newMainProps.power &&
    (meta18580.controls[0].on || meta18580.micControls[0].on) &&
    newMainProps.activePultZone.includes(true);

  // check pult
  // блокировика пульта, есть питание и есть связь с блоком и нажата сирена или микрофон
  if (newMainProps.blocking && isActivePult()) {
    newMainProps.pultSignal = true;
    newMainProps.message.soundZones.text = MESSAGE.blocking;
    meta18580.zoneControls.forEach((it, i) => {
      // зона активна если активна кнопка зоны или общая кнопка
      newMainProps.activeZone[i] = it.on || meta18580.controls[1].on;
    });

    // если есть активная зона и включена сирена
    if (meta18580.controls[0].on && newMainProps.activeZone.includes(true)) {
      // включена сирена
      newMainProps.track = `sirena`;
    }
  }

  // check Fire в зависимости от выбора алгоритма
  else if (newMainProps.fire) {
    switch (newMainProps.algoritm) {
      case `1`:
        if (!newMainProps.setSounds2ForFire) {
          // вывод первого сообщения в 1 зоны
          newMainProps.activeZone = [
            true,
            ...getFillArr(newMainProps.lineNumber - 1, false)
          ];
          newMainProps.track = `attention`;
          newMainProps.message.soundZones.text = MESSAGE.fire1Sound1Signal;
          newMainProps.loopNewSound = false;
          newMainProps.startTimerToSet2Fire = true;
        } else {
          // вывод второго сообщения во все зоны
          newMainProps.activeZone = newMainProps.fireZone;
          newMainProps.activeZone[0] = true;
          newMainProps.track = `fire`;
          newMainProps.message.soundZones.text = MESSAGE.fire1Sound2Signal;
        }

        break;
      case `2`:
        // вывод звука только в зоны пожара
        newMainProps.activeZone = newMainProps.fireZone;
        newMainProps.track = `fire`;
        newMainProps.message.soundZones.text = MESSAGE.fire2SoundSignal;
        newMainProps.setSounds2ForFire = false; // очистка от 2 сообщения пожара
        break;
      case `3`:
        newMainProps.setSounds2ForFire = false; // очистка от 2 сообщения пожара
        // обрабатывается только первая линия пожара
        // вывод звука во все зоны
        if (newMainProps.fireZone[0] === true) {
          newMainProps.activeZone = getFillArr(newMainProps.lineNumber, true);
          newMainProps.track = `fire`;
          newMainProps.message.soundZones.text = MESSAGE.fire3SoundSignal;
        } else {
          newMainProps.activeZone = getFillArr(newMainProps.lineNumber, false);
          newMainProps.fire = false;
        }
        break;
      default:
        break;
    }
  }

  // check сигнал ГО и ЧС проходит, если нет пожара и неисправности
  else if (newMainProps.goI4S) {
    newMainProps.setSounds2ForFire = false; // очистка от 2 сообщения пожара
    newMainProps.message.soundZones.text = MESSAGE.goI4S;
    newMainProps.activeZone = getFillArr(newMainProps.lineNumber, true);
    newMainProps.track = `go`;
    newMainProps.loopNewSound = true;
  }

  // // нет никаких активных режимов: пожар, неисправность, ГО
  else {
    newMainProps.setSounds2ForFire = false; // очистка от 2 сообщения пожара
    // проверка пульта в режиме "откл" приоритета
    if (newMainProps.blocking === false && isActivePult()) {
      newMainProps.pultSignal = true;
      newMainProps.message.soundZones.text = MESSAGE.blocking;

      meta18580.zoneControls.forEach((it, i) => {
        // зона активна если активна кнопка зоны или общая кнопка
        newMainProps.activeZone[i] = it.on || meta18580.controls[1].on;
      });

      // если есть активная зона и включена сирена
      if (meta18580.controls[0].on && newMainProps.activeZone.includes(true)) {
        // включена сирена
        newMainProps.track = `sirena`;
      }
    } else {
      newMainProps.message.soundZones.text = ``;
    }
  }

  newMainProps.signalStartSystem = newMainProps.activeZone.includes(true);

  if (!newMainProps.signalStartSystem) {
    // если нет сигнала убрать сообщение
    newMainProps.message.soundZones.text = ``;
  }

  return newMainProps;
};

// check pult meta 18580
export const getMeta18580 = ({ mainProps, meta18580 }) => {
  const newMeta18580 = { ...meta18580 };

  if (!mainProps.connectToPult || !mainProps.power) {
    // если нет соединение или нет питания => пульт не работает
    offLEDPult(newMeta18580);
  } else {
    newMeta18580.LEDs[0].on = mainProps.problem;
    newMeta18580.LEDs[1].on = true;
  }

  return newMeta18580;
};

export const setPowerOffSystem = ({ scheme, meta18580, mainProps }) => {
  const { elements } = scheme;
  scheme.settings.setSounds2ForFire = false;
  scheme.message.soundZones.text = ``;

  // offLEDBlock
  elements[22].LEDs.forEach(it => (it.on = false));

  // off signal
  elements[10].active = false; // signal  `Неисправность`
  elements[11].active = false; // signal  `Сигнал запуска системы`

  elements[12].active = false; // line 220
  elements[13].active = false; // line АКБ

  // disabled sound & active zone
  mainProps.activeZone = getFillArr(8, false);
  // set active speaker =  turn off sreaker
  for (let i = 14; i <= 21; i++) {
    elements[i].soundActive = false;
  }
};

export const offLEDPult = meta18580 => {
  meta18580.LEDs.forEach(it => (it.on = false));
  meta18580.micControls[0].on = false;
  meta18580.controls.forEach(it => (it.on = false));
  meta18580.zoneControls.forEach(it => (it.on = false));

  return meta18580;
};

const getActiveZone = (scheme, mainProps) => {
  return scheme.settings.numberSignals.map(it => mainProps.activeZone[it]);
};

const getMeta17820LCD = (scheme, mainProps) => {
  return {
    activeZone: getActiveZone(scheme, mainProps),
    pultSignal: mainProps.pultSignal,
    fire: mainProps.fire,
    goI4S: mainProps.goI4S,
    problem: mainProps.problem,
    blocking: mainProps.blocking,
    power: mainProps.power,
    offZummer: mainProps.offZummer,
    offVolume: mainProps.offVolume,
    signal: mainProps.signal
  };
};

export const getCheckScheme = ({
  schemeOld,
  meta18580,
  schemeIndex,
  mainProps,
  signal: { schemeID, signalID, value }
}) => {
  const scheme = {
    ...schemeOld,
    // props for LCD
    meta17820: getMeta17820LCD(schemeOld, mainProps)
  };
  const { elements } = scheme;

  // change signal in scheme
  if (schemeIndex === schemeID && value) {
    elements[signalID] = value;
  }

  // check ActiveZone
  // set active speaker = показать активные зоны (speaker)
  for (let i = 14; i <= 21; i++) {
    elements[i].soundActive = mainProps.lineToSpeakerErr[elements[i].zoneID]
      ? false
      : mainProps.activeZone[elements[i].zoneID];
  }

  // // check power system
  // // line 220V
  elements[12].active = mainProps.connectToPower;
  // // line АКБ
  elements[13].active = mainProps.connectToBoxAKB && !mainProps.connectToPower;

  // // new view
  // // 22 - mainProps
  // // 23 - meta18580
  // // диода на блоке 17820 = если нет питание не светятся
  if (elements[22] && elements[22].LEDs[0]) {
    elements[22].LEDs[0].on = mainProps.fire;
    elements[22].LEDs[1].on = mainProps.problem;
    elements[22].LEDs[2].on = true;
    elements[22].LEDs[5].on = mainProps.signalStartSystem;
  }

  // // signal
  if (elements[9].name === `toGoI4S`) {
    elements[9].active = mainProps.goI4S; // сигнал ГО и ЧС
  }
  elements[10].active = mainProps.problem; // signal  `Неисправность`
  elements[11].active = mainProps.signalStartSystem; // signal  `Сигнал запуска системы

  // scheme.elements = elements;
  scheme.mainProps = mainProps;

  scheme.message = mainProps.message;

  // check power system  = connect to power
  if (!mainProps.power) {
    setPowerOffSystem({ scheme, meta18580, mainProps });
  }

  if (schemeIndex === 0) {
    if (elements[23] && elements[23].type === `meta18580`) {
      elements[23] = meta18580;
    }
    if (elements[24] && elements[24].type === `meta18580`) {
      elements[24] = {
        ...meta18580,
        id: elements[24].id,
        top: elements[24].top
      };
    }
  }

  return { scheme };
};
