import {
  SOUND_SET_VOLUM,
  SOUND_SET_TRACK,
  SOUND_SET_USER_SOUND,
  SOUND_SET_USER_SOUND_TURN_ON
} from '../actionTypes';

export const setVolume = volumeValue => ({
  type: SOUND_SET_VOLUM,
  volumeValue
});

export const setTrack = (track, loop = true) => ({
  type: SOUND_SET_TRACK,
  track,
  loop
});

export const setUserSound = (fileName, fileData) => ({
  type: SOUND_SET_USER_SOUND,
  payload: {
    fileName,
    fileData
  }
});

export const setUserSoundTurnOn = soundOn => ({
  type: SOUND_SET_USER_SOUND_TURN_ON,
  payload: soundOn
});
