import React from 'react';
import { connect } from 'react-redux';
import { Redirect, Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';

import { setShowAsideMenu } from 'store/actions/appSettings.js';

import Link from '@material-ui/core/Link';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import logo from 'assets/img/logo140x50.png';

import { ROUTER_LINK } from 'utils/const/routerLink.js';

import classes from './Header.module.scss';

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      anchorEl: null,
      redirect: null
    };
  }

  onProfileMenuOpenClick = e => {
    this.setState(() => ({ anchorEl: e.currentTarget }));
  };
  onSetAsideMenuClick = () => {
    const showAsideMenu = !this.props.showAsideMenu;

    this.props.setShowAsideMenu(showAsideMenu);
  };
  onProfileMenuCloseClick = () => {
    this.setState(() => ({ anchorEl: null }));
  };

  onProfileClick = () => {
    if (this.props.auth.token && this.state.redirect === null) {
      const redirect = <Redirect to={ROUTER_LINK.private.userPage} />;
      this.setState(() => ({ redirect }));
    }
  };
  onLogoutClick = () => {
    if (this.props.auth.token && this.state.redirect === null) {
      const redirect = <Redirect to={ROUTER_LINK.logout} />;
      this.setState(() => ({ redirect }));
    }
  };

  render() {
    const { anchorEl } = this.state;
    const open = Boolean(anchorEl);

    return (
      <div className={classes.main}>
        <AppBar position="static">
          <Toolbar disableGutters={!this.props.isDesktop}>
            {// navIcon
            !this.props.isDesktop && (
              <IconButton
                color="inherit"
                aria-label="Menu"
                onClick={this.onSetAsideMenuClick}
              >
                <MenuIcon />
              </IconButton>
            )}

            <Link
              component={RouterLink}
              to={ROUTER_LINK.home}
              color="inherit"
              underline="none"
            >
              <img
                className={classes.logo}
                src={logo}
                title="meta"
                alt="Системы оповещения Meta"
              />
            </Link>
            <Typography
              className={classes.grow}
              variant="body1"
              color="inherit"
            >
              Системы речевого оповещения при пожаре, оборудование музыкальной
              трансляции, громкоговорители, театральная связь
            </Typography>

            {this.props.auth.token && (
              <div>
                <IconButton
                  aria-owns={open ? `menu-appbar` : null}
                  aria-haspopup="true"
                  onClick={this.onProfileMenuOpenClick}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: `top`,
                    horizontal: `right`
                  }}
                  transformOrigin={{
                    vertical: `top`,
                    horizontal: `right`
                  }}
                  open={open}
                  onClose={this.onProfileMenuCloseClick}
                >
                  <MenuItem onClick={this.onProfileClick}>Profile</MenuItem>
                  <MenuItem onClick={this.onLogoutClick}>Logout</MenuItem>
                </Menu>
              </div>
            )}
          </Toolbar>
        </AppBar>

        {this.state.redirect}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    showAsideMenu: state.appSettings.showAsideMenu,
    isDesktop: state.appSettings.isDesktop,
    auth: state.auth
  };
};

const mapDispatchToProps = dispatch => {
  return {
    setShowAsideMenu: showAsideMenu => dispatch(setShowAsideMenu(showAsideMenu))
  };
};

Header.propTypes = {
  showAsideMenu: PropTypes.bool.isRequired,
  isDesktop: PropTypes.bool.isRequired,
  auth: PropTypes.object.isRequired,
  setShowAsideMenu: PropTypes.func
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header);
