import React from 'react';
import { connect } from 'react-redux';
import { setUserSound, setUserSoundTurnOn } from 'store/actions/sound';
import { Switch } from '@material-ui/core';
import InputFile from 'components/UI/InputFile/InputFile';

import DELETE_SIGN from 'assets/img/delete-sign.png';
import classes from './PopupSetSound.module.scss';

class PopupSetSound extends React.Component {
  onChangeFile = ({ name, data }) => {
    if (!name && !data) {
      this.props.setUserSound('', '');
      this.props.setUserSoundTurnOn(false);
      return;
    }

    this.props.setUserSound(name, data);
  };

  setSoundOn = e => {
    if (!this.props.fileName) {
      return;
    }
    this.props.setUserSoundTurnOn(e.target.checked);
  };

  render() {
    const { fileName, onClose, soundOn } = this.props;

    return (
      <div className={classes.main}>
        <div className={classes.header}>
          <p className={classes.header_title}>Управление звуком</p>
          <img
            className={classes.header_closeIcon}
            src={DELETE_SIGN}
            alt="delete"
            onClick={onClose}
          />
        </div>

        <div className={classes.body}>
          <div className={classes.row}>
            <InputFile
              valueName={fileName}
              isFileAudio
              id="setSound"
              onChange={this.onChangeFile}
            />
          </div>

          {fileName && (
            <div className={classes.row}>
              <p>Включить звуковую трансляцию</p>
              <Switch
                checked={soundOn}
                onChange={this.setSoundOn}
                color="primary"
                value="checkedA"
                inputProps={{ 'aria-label': 'secondary checkbox' }}
              />
            </div>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  fileName: state.sound.fileName,
  soundOn: state.sound.soundOn
});

const mapDispatchToProps = {
  setUserSound,
  setUserSoundTurnOn
};

export default connect(mapStateToProps, mapDispatchToProps)(PopupSetSound);
