import React from 'react';
import { connect } from 'react-redux';
import PanelSettingsMeta17820 from 'components/Scheme/PanelSettingsMeta17820/PanelSettingsMeta17820.js';
import SchemeMeta17820 from 'components/Scheme/SchemeMeta17820/SchemeMeta17820.js';
import classes from './PanelShema.module.scss';

const pultNames = ['', '1pult', '2pulta'];
const blockNames = ['', '1blok', '2bloka', '3bloka', '4bloka', '5blokov'];

class PanelShema extends React.PureComponent {
  render() {
    const { config = {}, schemes, isSchemeDefault } = this.props;
    const { extraBlocks = [], activeNumberPult } = config;

    const numberBlock = extraBlocks.length + 1;
    const nameFile = `shema podklyucheniya META 17820_21_${blockNames[numberBlock]}_${pultNames[activeNumberPult]}.pdf`;
    const href = `/upload/scheme/${nameFile}`;

    return (
      <div className={classes.main}>
        <p className={classes.title}>Схема</p>
        <PanelSettingsMeta17820 />

        {!isSchemeDefault &&
          schemes.map((it, i) => (
            <SchemeMeta17820 key={`${i}_${Math.random()}`} schemeID={i} />
          ))}

        <div className={classes.downloadLink}>
          <a download={nameFile} href={href}>
            Скачать схему "{nameFile}"
          </a>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  config: state.configurator.config,
  schemes: state.scheme.schemes,
  isSchemeDefault: state.scheme.isSchemeDefault
});

export default connect(mapStateToProps)(PanelShema);
