import {
  SOUND_SET_VOLUM,
  SOUND_SET_TRACK,
  SOUND_SET_USER_SOUND,
  SOUND_SET_USER_SOUND_TURN_ON
} from '../actionTypes';

const initialState = {
  volumeValue: 10,
  track: `fileData`, // 'fileData' play track loading in fileData
  loop: true,

  // popup setSound
  fileName: '',
  fileData: '',
  soundOn: false
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SOUND_SET_VOLUM:
      return {
        ...state,
        volumeValue: action.volumeValue
      };
    case SOUND_SET_TRACK:
      return {
        ...state,
        track: action.track,
        loop: action.loop
      };
    case SOUND_SET_USER_SOUND:
      return {
        ...state,
        fileName: action.payload.fileName,
        fileData: action.payload.fileData
      };
    case SOUND_SET_USER_SOUND_TURN_ON:
      return {
        ...state,
        soundOn: action.payload
      };
    default:
      return state;
  }
};
