import React from "react"
import PropTypes from "prop-types"

import LED from "components/EditorComponents/LED/LED.js"

import classes from "./PanelLED.module.scss"

class PanelLED extends React.Component {
  render() {
    const { LEDs } = this.props;
    return (
      <div className={classes.main}>
        {LEDs.map((it, i) => <LED key={i} {...it} />)}
      </div>
    );
  }
}


PanelLED.propTypes = {
  LEDs: PropTypes.array.isRequired,
};

export default PanelLED;
