// import configurator from 'mockData/configurator.js';
import {
  CONFIGURATOR_SET_MAIN_BLOCK,
  CONFIGURATOR_SET_CONFIG,
  CONFIGURATOR_SET_CONFIGURED,
  CONFIGURATOR_SET_SHOW_TYPE
} from '../actionTypes';

const getInitState = () => ({
  mainBlock: 'МЕТА 17820', // TODO del ''
  configured: false, // all done - configurstor ready
  showType: 'shema', // TODO del '' // 'shema' 'specifications'
  config: {},
  specification: []
  // ...configurator
});

export default (state = getInitState(), action) => {
  switch (action.type) {
    case CONFIGURATOR_SET_MAIN_BLOCK:
      return {
        ...getInitState(),
        mainBlock: action.payload,
        configured: false,
        showType: '',
        config: { mainBlock: action.payload },
        specification: []
      };
    case CONFIGURATOR_SET_CONFIG: {
      const { payload = {} } = action;
      return {
        ...state,
        config: payload.config,
        specification: payload.specification
      };
    }
    case CONFIGURATOR_SET_CONFIGURED:
      return {
        ...state,
        configured: action.payload
      };

    case CONFIGURATOR_SET_SHOW_TYPE:
      return {
        ...state,
        showType: action.payload
      };
    default:
      return state;
  }
};
