import React from 'react';
import jsPDF from 'jspdf';
import 'assets/fonts/PTSans-normal';
import { useSelector } from 'react-redux';
// import TableSpecification from 'components/TableSpecification/TableSpecification';
import classes from './PanelSpecifications.module.scss';

const WIDTH = 185;
const X0 = 20;
const X1 = X0 + WIDTH;

const HEIGHT = 287;
const Y0 = 5;
const Y1 = Y0 + HEIGHT;

const HEADER_TEXT_LIST = [
  'Позиция',
  'Наименование и техническая характеристика',
  'Тип, марка, обозначение документа, опросного листа',
  'Код оборудования, изделия, материал',
  'Завод изготовитель',
  'Ед. изм.',
  'Количество',
  'Масса единицы, кг',
  'Примечание'
];
const VERTICAL_LINE_X = [9, 85, 110, 125, 152, 159, 167, 177, X1 - X0].map(
  it => X0 + it
);
const HEADER_COLUMN_WIDTH = VERTICAL_LINE_X.map((it, i) =>
  i === 0 ? it - X0 : it - VERTICAL_LINE_X[i - 1]
);
const HEADER_TEXT_CENTER_X = VERTICAL_LINE_X.map(
  (it, i) => it - HEADER_COLUMN_WIDTH[i] / 2
);

// количество строк текста
const HEADER_TEXT_TOP_Y = [2, 1, 4, 4, 2, 2, 3, 3, 3].map(
  it => 14.7 - it * 1.6
);

const TEXT_NUMBER_LIST = ['1', '2', '3', '4', '5', '6', '7', '8', '9'];
const TEXT_NUMBER_TOP_Y = Y0 + 18.5;

const TABLE_HEIGHT_ROW = 5;

const getdoc = specification => {
  const doc = new jsPDF();
  doc.setFont('PTSans'); // set font
  doc.setFontSize(10);

  // document.jsPDF = doc;

  // doc.addFont('PTSansNormal', 'custom', 'normal');
  // doc.setFont('custom');

  // https://github.com/MrRio/jsdoc
  // https://github.com/MrRio/jsPDF

  // .text('test 5', 5, 4); // text
  // .rect(x, y, w, h, style) // Empty square
  // doc.line(x0, y0, x1, y1);

  // ???
  // doc.table(1, 1, generateData(100), headers, { autoSize: true });
  // doc.addSvg(svg, 5, 50, 100, 100); // addSvg(SVG - Data, x, y, width, height);
  // doc.lines([[2,2],[-2,2],[1,1,2,2,3,3],[2,1]], 212,110, [1,1], 'F', false)

  // const canvas = document.getElementById('canvas');
  // console.log('TCL: clickSavedoc -> canvas', canvas);
  // console.log(
  //   'TCL: clickSavedoc -> canvas.toDataURL(png)',
  //   canvas.toDataURL(png)
  // );
  // doc.addImage(canvas.toDataURL(png), 'png', marginLeft, marginRight);

  // doc.save('Specification.doc');
  // doc.output('datauristring');

  // test square
  // doc.rect(5, 5, 5, 5);

  // рамка
  doc.rect(X0, Y0, WIDTH, HEIGHT);

  // таблица
  const renderSpecTable = (specification = []) => {
    // render Header
    const heightHeader = 20;
    const verticalLineY0 = Y0;
    const verticalLineY1 =
      verticalLineY0 + heightHeader + specification.length * 5;

    HEADER_TEXT_LIST.forEach((it, i) => {
      doc.setFontSize(9);
      doc.text(it, HEADER_TEXT_CENTER_X[i], HEADER_TEXT_TOP_Y[i], {
        align: 'center',
        maxWidth: `${HEADER_COLUMN_WIDTH[i] - 1}`
      });

      // vertical line
      if (i < HEADER_TEXT_LIST.length - 1) {
        const x = VERTICAL_LINE_X[i];
        doc.line(x, verticalLineY0, x, verticalLineY1);
      }
    });
    doc.line(X0, Y0 + 15, X1, Y0 + 15);

    TEXT_NUMBER_LIST.forEach((it, i) => {
      doc.text(it, HEADER_TEXT_CENTER_X[i], TEXT_NUMBER_TOP_Y, {
        align: 'center',
        maxWidth: `${HEADER_COLUMN_WIDTH[i]}`
      });
    });
    doc.line(X0, Y0 + 20, X1, Y0 + 20);

    // render table
    const tableY0 = Y0 + heightHeader + TABLE_HEIGHT_ROW;

    specification.forEach((it, i) => {
      const lineY = tableY0 + TABLE_HEIGHT_ROW * i;
      doc.line(X0, lineY, X1, lineY);

      const { type, row } = it;

      doc.setTextColor(type === 'title' ? '#888' : '#000'); // set font
      doc.setFontSize(type === 'title' ? 8 : 9);

      if (Array.isArray(row)) {
        row.forEach((text, i) => {
          doc.text(text, HEADER_TEXT_CENTER_X[i], lineY - 1.5, {
            align: 'center',
            maxWidth: `${HEADER_COLUMN_WIDTH[i]}`
          });
        });
      }
    });
  };

  const renderStamp = () => {
    // horizont
    const stampXStart = X0;
    const stampXMid = X1 - 120;
    const stampYStart = Y1 - 55;

    doc.line(stampXStart, Y1 - 55, X1, Y1 - 55);
    doc.line(stampXStart, Y1 - 50, stampXMid, Y1 - 50);

    doc.line(stampXStart, Y1 - 45, X1, Y1 - 45);
    doc.line(stampXStart, Y1 - 40, stampXMid, Y1 - 40);
    doc.line(stampXStart, Y1 - 35, stampXMid, Y1 - 35);

    doc.line(stampXStart, Y1 - 30, X1, Y1 - 30);
    doc.line(stampXStart, Y1 - 25, stampXMid, Y1 - 25);
    doc.line(stampXStart, Y1 - 20, stampXMid, Y1 - 20);

    doc.line(stampXStart, Y1 - 15, X1, Y1 - 15);
    doc.line(stampXStart, Y1 - 10, stampXMid, Y1 - 10);
    doc.line(stampXStart, Y1 - 5, stampXMid, Y1 - 5);

    // vertical
    doc.line(stampXStart, stampYStart, stampXStart, Y1);
    doc.line(stampXStart + 10, stampYStart, stampXStart + 10, Y1 - 5 * 6);
    doc.line(stampXStart + 20, stampYStart, stampXStart + 20, Y1);
    doc.line(stampXStart + 30, stampYStart, stampXStart + 30, Y1 - 5 * 6);
    doc.line(stampXStart + 40, stampYStart, stampXStart + 40, Y1);
    doc.line(stampXStart + 55, stampYStart, stampXStart + 55, Y1);
    doc.line(stampXMid, stampYStart, stampXMid, Y1);

    // small stamp right
    doc.line(X1 - 50, Y1 - 25, X1, Y1 - 25); // horizont
    doc.line(X1 - 50, Y1 - 30, X1 - 50, Y1 - 15); // vertical 1
    doc.line(X1 - 35, Y1 - 30, X1 - 35, Y1 - 15); // vertical 1
    doc.line(X1 - 20, Y1 - 30, X1 - 20, Y1 - 15); // vertical 2

    // text
    doc.setFont('PTSans'); // set font
    doc.setFontSize(10);

    const YText1 = Y1 - 31.3;
    doc.text('Изм.', X0 + 1, YText1);
    doc.text('Кол.', X0 + 12, YText1);
    doc.text('Лис.', X0 + 22, YText1);
    doc.text('№д.', X0 + 32, YText1);
    doc.text('Подп.', X0 + 43, YText1);
    doc.text('Дат.', X0 + 57, YText1);

    doc.text('Разработал.', X0 + 1, YText1 + 5);
    doc.text('Проверил', X0 + 1, YText1 + 10);
    doc.text('ГИП', X0 + 1, YText1 + 15);
    doc.text('Н.контр', X0 + 1, YText1 + 25);

    const YText2 = Y1 - 26.3;
    doc.text('Стадия', X1 - 48, YText2);
    doc.text('Лист', X1 - 31, YText2);
    doc.text('Листов', X1 - 16, YText2);

    const YText3 = Y1 - 18.5;
    doc.setFontSize(14);
    doc.text('Р', X1 - 44, YText3);
    doc.text('1', X1 - 29, YText3);
  };

  renderSpecTable(specification);
  renderStamp();

  return doc;
};

const getSrc4docView = specification => {
  const doc = getdoc(specification);
  return doc.output('datauristring');
};

// TODO задержка между скачиваниями
const clickSavedoc = specification => {
  const doc = getdoc(specification);
  doc.save('Specification.doc');
};

const PanelSpecifications = () => {
  const { specification = [] } = useSelector(state => state.configurator);
  const src = getSrc4docView(specification);
  return (
    <div className={classes.main}>
      <p className={classes.title}>Спецификация</p>

      {/* <TableSpecification /> */}

      <embed
        id="docView"
        src={src}
        type="application/pdf"
        download="Specification.pdf"
        style={{ overflow: 'auto', width: '100%', height: '1200px' }}
      ></embed>

      <p className={classes.downloadLink} onClick={() => clickSavedoc()}>
        Cкачать спецификацию "Specification.pdf"
      </p>
    </div>
  );
};

export default PanelSpecifications;
