import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { setPopup } from 'store/actions/popup';

import PanelControls from './PanelControls/PanelControls.js';
import PanelZoneControls from './PanelZoneControls/PanelZoneControls.js';
import Key from 'components/EditorComponents/Key/Key.js';

import mic from 'assets/img/components/mic-3-2.png';
import logo from 'assets/img/logo_black-2.png';

import classes from './Meta18580.module.scss';

const widths = {
  8: '420px',
  16: '470px',
  24: '530px',
  32: '590px'
};

class Meta18580 extends React.Component {
  onKeyClick = () => {
    const item = this.props.item;
    item.keyOn = !this.props.item.keyOn;

    this.props.onChange(this.props.item.id, item);
  };

  onPanelControlsClick = (field, value) => {
    const item = this.props.item;
    item[field] = value;

    this.props.onChange(this.props.item.id, item);
  };

  openPopupSetSound = () => {
    this.props.setPopup('setSound');
  };

  render() {
    const { item } = this.props;

    const lineStyle = {
      top: item.top || ``,
      left: item.left || ``,
      width: widths[item.lineNumber] || widths[8]
    };
    const controlsStyle = {
      marginRight: item.lineNumber > 8 ? '4px' : '24px'
    };

    return (
      <div className={classes.main} style={lineStyle}>
        <div className={classes.panel_top}>
          <img className={classes.mic} src={mic} title="mic" alt="Микрофон" />
          <img
            className={classes.logo}
            src={logo}
            title="logo"
            alt="Системы оповещения Meta"
          />
          <div className={classes.key}>
            <Key
              text1={`вкл`}
              text2={`откл`}
              text4={`Приоритет`}
              on={item.keyOn}
              onKeyClick={this.onKeyClick}
            />
          </div>
          <div className={classes.title}>
            <p className={classes.title_text}>{item.textName1}</p>
            <p className={classes.title_text}>{item.textName2}</p>
          </div>
        </div>

        <div className={classes.panel_bottom}>
          <div style={controlsStyle}>
            <PanelControls
              micControls={item.micControls[0]}
              LEDs={item.LEDs}
              controls={item.controls}
              lineNumber={item.lineNumber}
              onChange={this.onPanelControlsClick}
            />
          </div>
          <div className={classes.zoneControls}>
            <PanelZoneControls
              controls={item.zoneControls}
              lineNumber={item.lineNumber}
              onChange={this.onPanelControlsClick}
            />
          </div>
        </div>

        <p
          className={classes.setSound}
          tabIndex={0}
          onClick={this.openPopupSetSound}
          onKeyDown={this.openPopupSetSound}
        >
          Установить мелодию
        </p>
      </div>
    );
  }
}

const mapDispatchToProps = {
  setPopup
};

Meta18580.propTypes = {
  item: PropTypes.object,
  onChange: PropTypes.func.isRequired,
  setPopup: PropTypes.func
};

export default connect(null, mapDispatchToProps)(Meta18580);
