import React from 'react';
import DELETE_FOREVER from 'assets/img/delete-forever.png';
import classes from './InputFile.module.scss';

const DEFAULT_FILE_MAX_SIZE = 10000000;

class InputFile extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      inputValue: ``
    };
  }

  isImage = (fileType = '') => {
    if (this.props.isFileAudio) {
      return fileType.match('audio/*');
    }

    // default img type
    return fileType.match('image.*');
  };

  isDeleted = (value = false) => {
    if (value && value.name && value.name.length > 0) {
      return null;
    }
    return '';
  };

  onChangeFile = ({ files = [], fileMaxSize = DEFAULT_FILE_MAX_SIZE }) => {
    for (let i = 0; i < files.length; i++) {
      const it = files[i];
      const fileName = it.name;
      const fileSize = it.size;
      const fileType = it.type;

      if (!this.isImage(fileType)) {
        this.setState({ inputValue: `` });
        return this.onChange({});
      }

      if (fileSize < fileMaxSize) {
        const reader = new FileReader();
        reader.onload = e => {
          this.onChange({
            name: fileName,
            data: e.target.result,
            size: fileSize,
            type: fileType
          });
          this.setState({ inputValue: `` });
        };

        reader.readAsDataURL(it);
      }
    }

    return null;
  };

  onChange = e => {
    if (!this.props.onChange) {
      return;
    }
    this.props.onChange(e);
  };

  render() {
    const {
      valueName,
      onChange,
      type,
      fileMaxSize,
      typeFiles,
      title,
      multiple,
      ...props
    } = this.props;

    const showIcon = valueName;

    return (
      <div className={classes.main}>
        <div className={classes.box}>
          <input
            className={classes.input}
            value={this.state.inputValue}
            type="file"
            id={`InputFile_${props.id}`}
            multiple={multiple}
            disabled={props.disabled}
            onChange={e =>
              this.onChangeFile({
                files: e.target.files,
                fileMaxSize,
                typeFiles,
                onChange
              })
            }
          />

          <label className={classes.label} htmlFor={`InputFile_${props.id}`}>
            <span className={classes.labelTitle}>Выберите файл</span>
            <span className={classes.labelTitle}>{valueName}</span>
          </label>

          {showIcon && (
            <div className={classes.boxIcon} onClick={() => this.onChange({})}>
              <img
                className={classes.boxIcon_delete}
                src={DELETE_FOREVER}
                alt="delete"
              />
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default InputFile;
