import React from 'react';
import ActivePaperBtn from 'components/UI/ActivePaperBtn/ActivePaperBtn';
import meta17821Img from 'assets/img/components/17821_m.png';
import classes from './PanelMeta17820ExtraBlock.module.scss';

const PanelMeta17820ExtraBlock = ({
  blocks,
  index,
  onActiveBlockChange,
  onExtraBlocksDelete
}) => {
  const currentNumber = `${index + 1}.`;
  return (
    <div className={classes.main}>
      <p className={classes.number}>{currentNumber}</p>
      {blocks.map(it => (
        <div key={it.title} className={classes.blockStepItem}>
          <ActivePaperBtn
            isActive={it.active}
            {...it}
            img={meta17821Img}
            width="150px"
            onChange={() =>
              onActiveBlockChange && onActiveBlockChange(index, it.title)
            }
          />
        </div>
      ))}

      <div className={classes.blockStepItem}>
        <ActivePaperBtn
          title="удалить"
          small
          onChange={() => onExtraBlocksDelete && onExtraBlocksDelete(index)}
        />
      </div>
    </div>
  );
};

export default PanelMeta17820ExtraBlock;
