import sirensList from 'utils/const/list/sirensList';
import { setNewScheme } from 'store/actions/scheme';
import {
  CONFIGURATOR_SET_MAIN_BLOCK,
  CONFIGURATOR_SET_CONFIG,
  CONFIGURATOR_SET_CONFIGURED,
  CONFIGURATOR_SET_SHOW_TYPE
} from '../actionTypes';

const NAME_META = 'ЗАО НПП МЕТА';
const EMPTY_ROW = ['', '', '', '', '', '', '', '', ''];
const TITLE_NUMBER = ['А', 'Б', 'В', 'Г', 'Д', 'Е', 'Ж', 'З', 'И'];

export const setMainBlock = mainBlock => ({
  type: CONFIGURATOR_SET_MAIN_BLOCK,
  payload: mainBlock
});

const setConfig = config => ({
  type: CONFIGURATOR_SET_CONFIG,
  payload: config
});

const setConfigured = payload => ({
  type: CONFIGURATOR_SET_CONFIGURED,
  payload
});

export const setShowType = showType => dispatch => {
  if (showType === 'shema') {
    dispatch(setNewScheme());
  }

  dispatch({
    type: CONFIGURATOR_SET_SHOW_TYPE,
    payload: showType
  });
};

const getTitle = ({ ctrTitle }) => ({
  type: 'title',
  row: [
    ``,
    `Оборудование блок ${TITLE_NUMBER[ctrTitle]}`,
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
});

const getMeta17820 = ({ ctr, name }) => {
  return {
    row: [
      `${ctr}`,
      'Центральный блок Системы речевого оповещения',
      name,
      '',
      NAME_META,
      'шт.',
      '1',
      '',
      ''
    ]
  };
};

const getAKBBox = ({ ctr }) => ({
  row: [
    `${ctr}`,
    'Бокс под аккумуляторы',
    'МЕТА 17901',
    '',
    NAME_META,
    'шт.',
    '1',
    '',
    ''
  ]
});

const getAKB = ({ blockName, ctr }) => {
  const akbName = blockName === 'МЕТА 17821' ? '40 А / ч' : '26 А / ч';

  return {
    row: [
      `${ctr}`,
      'Аккумулятор 12В',
      akbName,
      '',
      NAME_META,
      'шт.',
      '2',
      '',
      ''
    ]
  };
};

const getPultRow = ({ ctr, lineNumber = 8, activeNumberPult = '1' }) => {
  return {
    row: [
      `${ctr}`,
      `Пульт микрофонный на ${lineNumber} направлений`,
      `МЕТА-18580-${lineNumber}`,
      '',
      NAME_META,
      'шт.',
      activeNumberPult,
      '',
      ''
    ]
  };
};
const getSirensRow = ({ ctr, sirensType, activeNumberSirene }) => {
  const sirens = sirensList.find(it => it.type === sirensType) || {};
  return {
    row: [
      `${ctr}`,
      `${sirens.title || ''}`,
      sirensType,
      '',
      NAME_META,
      'шт.',
      activeNumberSirene,
      '',
      ''
    ]
  };
};

const getSpecification = (config = {}) => {
  const res = [];
  let ctr = 1;
  let ctrRowID = 0;
  let ctrTitle = 0; // А Б В Г
  let ctrPult = 0; //

  const addRow = ({ row, type, isEmpty }) => {
    res.push({
      id: `${ctrRowID}`,
      type,
      row
    });
    ctrRowID++;

    if (!isEmpty) {
      ctr++;
    }
    if (type === 'title') {
      ctrTitle++;
    }
  };

  const addEmptyRow = () => {
    addRow({ row: EMPTY_ROW, isEmpty: true });
  };

  const addPult = () => {
    if (ctrPult < +config.activeNumberPult) {
      addRow(
        getPultRow({
          ctr,
          lineNumber: config.lineNumber,
          activeNumberPult: config.activeNumberPult
        })
      );
      ctrPult++;
    }
  };

  if (config.mainBlock === 'МЕТА 17820' || config.mainBlock === 'МЕТА 17821') {
    addRow(getTitle({ ctrTitle }));
    addRow(getMeta17820({ name: config.mainBlock, ctr }));
    addRow(getAKBBox({ ctr }));
    addRow(getAKB({ blockName: config.mainBlock, ctr }));
    addPult();
  }

  if (config.extraBlocks && Array.isArray(config.extraBlocks)) {
    config.extraBlocks.forEach((it = [], i) => {
      const block = it.find(it => it.active) || {};
      const name = block.title;

      // addEmptyRow();
      addRow(getTitle({ ctrTitle }));
      addRow(getMeta17820({ name, ctr }));
      addRow(getAKBBox({ ctr }));
      addRow(getAKB({ blockName: name, ctr }));
    });
  }

  if (config.sirensType) {
    addEmptyRow();
    addRow(getSirensRow({ ...config, ctr }));
  }

  return res;
};

export const setNewConfig = (config = {}) => (dispatch, getState) => {
  // const { configured = false } = getState().configurator;
  // if (!configured && !config.activeNumberPult) {
  //   return;
  // }

  if (config.activeNumberPult) {
    dispatch(setConfigured(true));
  }

  const specification = getSpecification(config);
  dispatch(setConfig({ config, specification }));
};
