import React from 'react';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';

import Btn1LED from 'components/EditorComponents/Btn1LED/Btn1LED.js';

import classes from './PanelZoneControls.module.scss';

const widths = {
  8: '150px',
  16: '200px',
  24: '275px',
  32: '350px'
};

class PanelZoneControls extends React.Component {
  onBtn1LEDClick = (id, value) => {
    const controls = this.props.controls;
    controls[id] = value;

    this.props.onChange(`zoneControls`, controls);
  };
  render() {
    const { lineNumber, controls = [] } = this.props;
    const lineStyle = {
      width: widths[lineNumber] || widths[8]
    };
    return (
      <div className={classes.main} style={lineStyle}>
        <div className={classes.title}>
          <Typography variant="body1" align="center">
            ЗОНЫ
          </Typography>
        </div>

        {controls.map((it, i) => (
          <Btn1LED key={i} item={it} onChange={this.onBtn1LEDClick} />
        ))}
      </div>
    );
  }
}

PanelZoneControls.propTypes = {
  controls: PropTypes.array.isRequired,
  onChange: PropTypes.func.isRequired
};

export default PanelZoneControls;
