import React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import LCDSymbolSVG from 'components/EditorComponents/LCDSymbolSVG/LCDSymbolSVG.js';
import classes from './LCDLine0.module.scss';

const LCDLine0 = ({ power, textLine, schemeID }) => {
  const { timeToTextLine, timeToTextLineBR } = useSelector(state => state.time);
  const text = power
    ? schemeID === 0
      ? textLine || timeToTextLine
      : textLine || timeToTextLineBR
    : ' ';

  return (
    <div className={classes.textRow}>
      {/* 24 characters */
      text
        .slice(0, 24)
        .split(``)
        .map((it, i) => (
          <div key={`${it}_${i}`} className={classes.textRowItem}>
            <LCDSymbolSVG LCDSymbol={it} />
          </div>
        ))}
    </div>
  );
};

LCDLine0.propTypes = {
  power: PropTypes.bool,
  schemeID: PropTypes.number,
  textLine: PropTypes.string
};

export default React.memo(LCDLine0);
