import React from 'react';
import { connect } from 'react-redux';
import { setDefaultScheme } from 'store/actions/scheme';
import Typography from '@material-ui/core/Typography';

import LayoutPage from 'layout/LayoutPage/LayoutPage.js';
import SchemeMeta17820 from 'components/Scheme/SchemeMeta17820/SchemeMeta17820.js';
import PanelSettingsMeta17820 from 'components/Scheme/PanelSettingsMeta17820/PanelSettingsMeta17820.js';

import classes from './PageMeta17820.module.scss';

class PageMeta17820 extends React.PureComponent {
  componentDidMount() {
    this.props.setDefaultScheme();
  }

  render() {
    const { settings = {}, isSchemeDefault } = this.props;
    return (
      <LayoutPage minWidth={settings.width}>
        <Typography variant="h1" gutterBottom>
          Системы речевого оповещения при пожаре МЕТА 17820 / МЕТА 17821
        </Typography>

        <div className={classes.settings}>
          <PanelSettingsMeta17820 />
        </div>

        {isSchemeDefault ? (
          <div className={classes.editor}>
            <SchemeMeta17820 schemeID={0} />
          </div>
        ) : null}
      </LayoutPage>
    );
  }
}

const mapStateToProps = state => ({
  isSchemeDefault: state.scheme.isSchemeDefault
});

const mapDispatchToProps = {
  setDefaultScheme
};

export default connect(mapStateToProps, mapDispatchToProps)(PageMeta17820);
