import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import attention from 'assets/sounds/attention.wav';
import fire from 'assets/sounds/fire.wav';
import gong from 'assets/sounds/gong.wav';
import go from 'assets/sounds/go.wav';
import sirena from 'assets/sounds/sirena.wav';

// конверетер аудио
// https://www.online-convert.com/
// 8кб

// скачивание свука https://wav-library.net/

class Audio extends React.Component {
  constructor(props) {
    super(props);

    this.audioobject = null;
  }
  componentDidMount() {
    this.audioobject = document.querySelector(`#audio`);
    this.setVolume(this.props.sound.volumeValue / 100);
  }
  componentDidUpdate() {
    this.setVolume(this.props.sound.volumeValue / 100);
  }
  setVolume(volumeValue) {
    this.audioobject.volume = volumeValue;
  }
  getTrack(track) {
    if (track === 'fileData' && this.props.sound.fileData) {
      return this.props.sound.fileData;
    }

    const trackList = {
      attention,
      fire,
      gong,
      go,
      sirena
    };

    return trackList[track];
  }
  render() {
    const { sound } = this.props;

    return (
      <div>
        <audio
          id="audio"
          loop={sound.loop}
          autoPlay
          src={this.getTrack(sound.track) || ``}
        />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    sound: state.sound
  };
};

Audio.propTypes = {
  sound: PropTypes.object,
  onControllerChange: PropTypes.func
};

export default connect(mapStateToProps)(Audio);
